import React from 'react';
import Style from './PaymentBar.module.css'
import ToolBar from '../ListBar/ToolBar/ToolBar'
import PaymentSubHead from './PaymentSubHead/PaymentSubHead'
import PaymentListElement from '../PaymentListElement/PaymentListElement'
function PaymentBar(props) {
  return (
      <div className={ Style.PaymentBar }>
          <ToolBar head="Payments" searchHandler={props.SearchHandler} searchString={props.searchString} placeholder="Search Store Name" />
          <PaymentSubHead />
          <div className={Style.list}>
              {props.list.map((ele,index)=>{
                      let today = new Date()
                      let todayTotal = 0
                      today.setDate(today.getDate() - 7) 
                      today.setHours(0,0,0,0)
                      if(ele.dailyCommissionList[today]){
                          todayTotal = ele.dailyCommissionList[today]["commissionAmt"]
                      }
                if(ele.storeName.includes(props.searchString)){
                    return(<PaymentListElement img={ele.bannerImage} payToday = {todayTotal} storeName={ele.storeName} commission={ele.commission} totalDue = {ele.totalDueCommission}  clicked={()=>{return props.sellerChangeHandler(index)}} active={props.activeSeller===index?true:false}/>)
                }
                else{
                  return null
                }
              })}
          </div>
      </div>
  );
}

export default PaymentBar;