import React from 'react'
import Style from './OrdersBar.module.css'
import OrderSubHead from './OrderSubHead/OrderSubHead'
import ToolBar from '../../component/ListBar/ToolBar/ToolBar'
import OrderListElement from '../OrderListElement/OrderListelement'
const OrdersBar = (props)=>{
    return(
    <div className={Style.OrdersBar}>
          <ToolBar head="Orders" searchString={props.searchString} searchHandler={props.searchHandler} placeholder="search Order Id"/>
          <OrderSubHead />
          <div className={Style.list}>
              {props.orders.map((ele,index)=>{
                if(ele.orderId.includes(props.searchString)){
                  if(index===props.activeOrder){
                    return(<OrderListElement key={index} merchants={props.merchants} clicked={()=>{return props.orderChangeHandler(index)}} products={ele.products} payment={ele.transactionId} active date={ele.createdAt} status={ele.status}  merchant={ele.merchantId}  id={ele.orderId} mode={ele.isOnline}/>)
                  }
                  else{
                    return(<OrderListElement key={index} merchants={props.merchants} clicked={()=>{return props.orderChangeHandler(index)}} products={ele.products} payment={ele.transactionId} date={ele.createdAt} status={ele.status}  merchant={ele.merchantId}  id={ele.orderId} mode={ele.isOnline}/>)
                  }
                }
              else{
                return null
              }
              })}
          </div>
    </div>
    )
}

export default OrdersBar