import React from 'react'
import Style from './SellerElementList.module.css'
import SmallImage from '../SmallImage/SmallImage'
const SellerListElement = (props)=>{
    let styled = {
        backgroundColor:props.active?"#EFF3F6":null
    }
    return(
    <ul onClick={props.clicked} style={styled} className={ Style.SellerListElement }>
        <li><SmallImage addImgHandler={()=>{return null}} imgUrl={props.img} /> </li>
        <li>{props.storeName}</li>
        <li>{props.openingTime}</li>
        <li>{props.closingTime}</li>
        <li>{props.products}</li>
        <li>{props.orders}</li>
    </ul>
    )
}

export default SellerListElement