import React, { Fragment } from 'react'
import SmallImage from '../SmallImage/SmallImage'
import Style from './CustomerDetails.module.css'

const CustomerDetails = (props)=>{
    let real = (    
    <div className={Style.CustomerDetails}>
        <h1>Customer Details</h1>
        <div className={Style.customer}>
                <h3>Customer Details:</h3>
                <p>Name: {props.customer.userName}</p>
                <p>Phone: {props.customer.userPhone}</p>
                <p>Email: {props.customer.userEmail}</p>
                <h2>Delivery Address:</h2>
                <p>{props.customer.address}</p>
                {/* <h1>STATUS: {props.order.status}</h1> */}
            </div>
        <h3>Past Orders</h3>
        {props.orders?props.orders.map(order =>{
            return(
                <div className={Style.orderDetail}> 
                    <h3>ORDER ID : {order.orderId}</h3>
                    <div className={Style.products}>
                        {order.products.map(product=>{
                                return(<div>
                                            <SmallImage  imgUrl={product.productImage}/>
                                        <div>
                                        <div>{product.name}</div>
                                        <div>₹{product.price} </div>
                                        <div> Sold By: {props.merchants[order.merchantId]} </div>
                                        <div> <span> Size: {product.size} </span> <span>Qty: { product.quantity }</span> </div>
                                        </div>
                                    </div>
                                )
                            })}
                    </div>
                </div>
            )
        }):null}
        </div>
    );
    return(<Fragment>
        {props.customer.userId===undefined?null:real}
        </Fragment>
    )
}

export default CustomerDetails