import React from 'react'
import InputTag from '../InputTag/InputTag'
import Style from './ExtraDiv.module.css'

const ExtraDiv = (props) =>{
    return(
        <div className={Style.ExtraDiv}>
            <h1>{props.name}</h1>
            <div className={Style.List}>
                {props.values.map((val,ind)=>{
                    return(<InputTag inputChange={(e,index)=>{props.inputHandler(e,index,props.name)}} width="50%" height="30px" type="text" tagName={ind} value={val} />)
                })}
                <InputTag inputChange={props.addInputHandler} width="50%" height="30px" type="text" tagName={props.name} value={props.add[props.name]} />
                <button onClick={()=>{props.updateList(props.name)}}>Update</button>
            </div>
        </div>
    )
}

export default ExtraDiv