import React, { Component, Fragment } from 'react'
import Style from './Seller.module.css'
import SellerBar from '../../component/SellerBar/SellerBar'
import FixedBar from '../../component/FixedBar/FixedBar'
import SellerDetails from '../../component/SellerDetails/SellerDetails'
import firebase from '../../Firebase/index'
import InputForm from '../../component/InputForm/InputForm'
import ImageInput from '../../component/ImageInput/ImageInput'
class Products extends Component{
    addImgHandler = ()=>{
        this.setState({addImg:true})
    }
    uploadImgHandler = (file)=>{
        let ref = firebase.storage.ref()
        let fileName = new Date() + '-' + file.name
        let metadata = {contentType:file.type}
        let task = ref.child("data/user/0/com.vendor_app_shopping/cache"+fileName).put(file,metadata)
        task
        .then(snapShot => snapShot.ref.getDownloadURL()
        .then(url =>{
            let bannerImage = url 
            let sellerCopy = {
                ...this.state.seller,
                bannerImage:bannerImage
            }
            this.setState({seller:sellerCopy,addImg:false})
        }))

    }
    SellerChangeHandler = (index)=>{
        this.setState({seller:this.state.sellers[index],activeSeller:index,addCat:false})
    }
    SearchHandler = (e)=>{
        this.setState({search:e.target.value})
    }
    inputChangeHandler = (e,tag) =>{
        let sellerCopy = {
            ...this.state.seller,
            categories:[...this.state.seller.categories]
        }
        sellerCopy[tag] = e.target.value
        this.setState({seller:sellerCopy})
    }
    sellerSaveChange = ()=>{
        let seller = {...this.state.seller}
        firebase.db.collection('merchant').doc(this.state.seller.merchantId).set({
            ...seller
        })
        .then(()=>{
            this.getSellers()
        })
        
    }
    sellerSaveNew = ()=>{
        firebase.db.collection('merchant').add({
            ...this.state.seller
        })
        .then((docRef)=>{
            this.getSellers()
            this.setState({activeAdd:false})
        })
    }
    newSellerHandler = ()=>{
        let seller = {
            address:"",
            bannerImage:null,
            categories:[],
            cgst:0,
            closingTiming:"",
            openingTiming:"",
            igst:0,
            invoiceNumber:0,
            landlineNumber:0,
            onlinePaymentCharge:0,
            shippingCharge:0,
            storeName:"",
            userName:"",
            userPhone:"",
            commission:0,
            totalCommissionDue:0,
            phoneNumber2:"",
            dailyCommissionList:{}
        }
        this.setState({activeAdd:true,seller:seller,activeSeller:null})
    }
    getProducts = ()=>{
        let products = {}
        firebase.db.collection('products').get()
        .then(querySnapshot => {
        querySnapshot.forEach( doc => {
            let data = {
                ...doc.data(),
                productId: doc.id
            }
            if(data.product){}
            else{
                if(products[data.merchantId]){
                    products[data.merchantId]+=1
                }
                else{
                    products[data.merchantId]=1
                }
            }
        })
        this.setState({products:products})
        this.getOrders()
      })
    }
    getOrders = ()=>{
        let orders = {}
        firebase.db.collection('orders').get()
        .then(querySnapshot => {
        querySnapshot.forEach( doc => {
            let data = {
                ...doc.data()
            }
                if(orders[data.merchantId]){
                    orders[data.merchantId]+=1
                }
                else{
                    orders[data.merchantId]=1
                }
        })
        this.setState({orders:orders})
        console.log(orders)
      })
    }
    getSellers = ()=>{
        let sellers=[]
        firebase.db.collection('merchant').get()
        .then(querySnapshot =>{
            querySnapshot.forEach(doc=>{
                let data = {
                    ...doc.data(),
                    merchantId:doc.id
                }
                sellers.push(data)
            })
            this.setState({sellers:sellers})
            this.getProducts()
        })
    }
    constructor(props){
        super(props);
        this.state = {
            sellers:null,
            allCategories:null,
            seller:{
                address:null,
                bannerImage:null,
                categories:[],
                cgst:null,
                closingTiming:null,
                openingTiming:null,
                igst:null,
                invoiceNumber:null,
                landlineNumber:null,
                merchantId:null,
                onlinePaymentCharge:null,
                shippingCharge:null,
                storeName:null,
                userName:null,
                userPhone:null,
                commission:0,
                phoneNumber2:""
            },
            activeSeller:null,
            products:null,
            orders:null,
            activeAdd:false,
            search:'',
            addCat:false,
            addImg:false
            }
        this.getSellers()
      firebase.db.collection('other').doc('categories').get()
      .then(doc => {
          let categories = ["none",]
          
            doc.data().categories.forEach(cat=>{
                if( cat.subCat ){
                    categories = [...categories, ...cat.subCat]
                }
                categories.push(cat.name)
            })
            let complete = []
            for(let i=0;i<categories.length;i++){
                complete.push({
                    name:categories[i],
                    value:categories[i]
                })
            }
          this.setState({allCategories:complete})
      })
    }
    showAddCat = () =>{
        this.setState({addCat:true})
    }
    addCat = (value)=>{
        let categories = [...this.state.seller.categories,value]
        let sellerCopy = {
            ...this.state.seller,
            categories:categories
        }
        this.setState({seller:sellerCopy,addCat:false})
    }
    CloseModal = ()=>{
        if(this.state.addImg===true){
            this.setState({addImg:false})
        }
        else{
            this.setState({activeAdd:false})
        }
    }
    render(){
        return(
            <Fragment>
                {this.state.orders===null?null:
                (<div className={ Style.Seller }>
                    <SellerBar list={this.state.sellers} products={this.state.products} orders={this.state.orders} sellerChangeHandler={this.SellerChangeHandler} searchString={this.state.search} SearchHandler={this.SearchHandler} activeSeller={this.state.activeSeller} newSellerHandler={this.newSellerHandler} />
                    <FixedBar>
                        {this.state.activeAdd?null:<SellerDetails addImgHandler={this.addImgHandler} seller={this.state.seller} showAddCat={this.showAddCat} addCat={this.addCat} addcat={this.state.addCat} allCategories={this.state.allCategories} inputChangeHandler={this.inputChangeHandler} sellerSaveChange={this.sellerSaveChange} />}
                    </FixedBar>
                    {this.state.activeAdd?(<InputForm CloseModal={this.CloseModal}>
                        <SellerDetails addImgHandler={this.addImgHandler} seller={this.state.seller} showAddCat={this.showAddCat} addCat={this.addCat} addcat={this.state.addCat} allCategories={this.state.allCategories} inputChangeHandler={this.inputChangeHandler} sellerSaveChange={this.sellerSaveNew}/>
                    </InputForm>):null}
                    {this.state.addImg?<InputForm CloseModal={this.CloseModal}><ImageInput uploadImgHandler={this.uploadImgHandler}/></InputForm>:null}
                </div>)}
                    
            </Fragment>
        );
    }
}

export default Products;