import React,{Component} from 'react'
import Style from './Extras.module.css'
import ExtraDiv from '../../component/ExtraDiv/ExtraDiv'
import firebase from '../../Firebase/index'

class Extras extends Component{
    constructor(props){
        super(props)
        this.state = {
            extras:{
                brands:null,
                color:null,
                fabrics:null,
                heel:null,
                neckline:null,
                occasion:null,
                pattern:null,
                rise:null,
                size:null,
                sizeDefault:null,
                sizeFootwear:null,
                sleeves:null,
                sole:null,
                style:null,
                work:null
            },
            add:{
                brands:'',
                color:'',
                fabrics:'',
                heel:'',
                neckline:'',
                occasion:'',
                pattern:'',
                rise:'',
                size:'',
                sizeDefault:'',
                sizeFootwear:'',
                sleeves:'',
                sole:'',
                style:'',
                work:''
            }
        }
        this.getAllExtras()
    }
    getAllExtras = ()=>{
        Object.keys(this.state.extras).forEach(ext=>{
            this.getExt(ext)
        })
    }
    getExt = (ext) => {
        firebase.db.collection('other').doc(ext).get()
        .then(doc =>{
            let extras = {...this.state.extras}
            extras[ext] = doc.data()[ext]
            this.setState({extras:extras})
        })
    }
    listChangeHandler = (e,index,name)=>{
        let extrasCopy={...this.state.extras}
        let extrasCopyName=[...extrasCopy[name]]
        extrasCopyName[index] = e.target.value
        extrasCopy[name]=extrasCopyName
        this.setState({extras:extrasCopy})
    }
    addChangeHandler = (e,name)=>{
        let addCopy={...this.state.add}
        addCopy[name] = e.target.value
        this.setState({add:addCopy})
    }
    updateList = (name)=>{
        let extCopy = {...this.state.extras}
        let extName = [...extCopy[name]]
        if(this.state.add[name]!==''){
            extName.push(this.state.add[name])
        }
        let push = {}
        push[name] = extName
        firebase.db.collection('other').doc(name).set(push)
        .then(()=>{
            this.getExt(name)
            let addCpy = {...this.state.add}
            addCpy[name] = ''
            this.setState({add:addCpy})
        })
    }
    render(){
        return(
        <div className={Style.Extras}>
            {Object.keys(this.state.extras).map(ele=>{
                return(this.state.extras[ele]===null?null:<ExtraDiv updateList={this.updateList} name={ele} add={this.state.add} addInputHandler={this.addChangeHandler} inputHandler={this.listChangeHandler} values={this.state.extras[ele]}/>)
            })}
        </div>
        );
    }
}

export default Extras