import React from 'react'
import Style from './PaymentListElement.module.css'
import SmallImage from '../SmallImage/SmallImage'
const PaymentListElement = (props)=>{
    let styled = {
        backgroundColor:props.active?"#EFF3F6":null
    }
    return(
    <ul onClick={props.clicked} style={styled} className={ Style.PaymentListElement }>
        <li><SmallImage addImgHandler={()=>{return null}} imgUrl={props.img} /> </li>
        <li>{props.storeName}</li>
        <li>{props.commission}</li>
        <li>{props.totalDue}</li>
        <li>{props.payToday}</li>
        <li>{props.orders}</li>
    </ul>
    )
}

export default PaymentListElement