import React, {Component, Fragment} from 'react'
import OdersBar from '../../component/OrdersBar/OdersBar'
import FixedBar from '../../component/FixedBar/FixedBar'
import Style from './Orders.module.css'
import OrderDetails from '../../component/OrderDetails/OrderDetails'
import firebase from '../../Firebase/index'
import InputForm from '../../component/InputForm/InputForm'
import InputTag from '../../component/InputTag/InputTag'
class Orders extends Component{
    orderChangeHandler = (index)=>{
        this.setState({order:this.state.orders[index],activeOrder:index})
        console.log(this.state.order)
    }
    SearchHandler = (e)=>{
        this.setState({search:e.target.value})
    }
    upload = ()=>{
        let order = {...this.state.order}
        delete order.orderId
        firebase.db.collection('orders').doc(this.state.order.orderId).set({
            ...order
        })
        .then(()=>{
            console.log("success")
            this.setState({addShow:false,order:{products:[]},activeOrder:null})
            this.getOrders()
        })
    }
    constructor(props){
        super(props);
        this.state = {
            orders:null,
            merchants:null,
            customers:{},
            order:{
                products:[]
            },
            activeOrder:null,
            search:'',
            addShow:false
        }
        this.getOrders()
        this.getSeller()
    }
    getOrders = ()=>{
        let customer = {}
        let orders=[]
        firebase.db.collection('orders').get()
        .then(querySnapshot => {
            querySnapshot.forEach( doc => {
                let data={
                    ...doc.data(),
                    orderId:doc.id
                }
              orders.push(data)
              firebase.db.collection('User').doc(doc.data().userId).get()
              .then(userDoc =>{
                customer[userDoc.id] = userDoc.data()
              })
            })
            this.setState({orders:orders,customers:customer})
          })
    }
    inputChangeHandler = (e,tag)=>{
        let orderCopy={
            ...this.state.order
        }
        console.log(orderCopy)
        orderCopy[tag] = e.target.value
        this.setState({order:orderCopy})
    }
    modifyDelivery = ()=>{
        // console.log(this.order.address)
        this.setState({addShow:!this.state.addShow})
    }
    getSeller = ()=>{
        firebase.db.collection('merchant').get()
        .then(
            querySnapshot =>{
                let merchants={}
                querySnapshot.forEach(doc=>{
                    merchants[doc.id]=doc.data().storeName
                })
                this.setState({merchants:merchants})
            }
        )
    }
    render(){
        return(<Fragment>
            {this.state.orders===null||this.state.merchants===null?null:(            
            <div className={Style.Orders}>
                <OdersBar searchHandler={this.SearchHandler} searchString={this.state.search} merchants={this.state.merchants} orderChangeHandler = {this.orderChangeHandler} activeOrder={this.state.activeOrder} orders={this.state.orders} />
                <FixedBar>
                    {this.state.order.orderId?<OrderDetails modifyDelivery={this.modifyDelivery} merchants={this.state.merchants} customer={this.state.customers[this.state.order.userId]} order={this.state.order} />:null}
                </FixedBar>
            </div>
            )}
            {this.state.addShow?<InputForm CloseModal={this.modifyDelivery}><InputTag inputChange={this.inputChangeHandler} width="100%" height="80px" name="address" type="box" tagName="address" lable="Delivery Address" value={this.state.order.address}/><button onClick={(e)=>{this.upload(e)}} className={Style.Button}>Submit</button></InputForm>:false}
          </Fragment>

        )
    }
}

export default Orders