import React from 'react'
import Style from './CustomerListElement.module.css'
import SmallImage from '../SmallImage/SmallImage'

const CustomerListElement = (props)=>{
    let styled = {
        backgroundColor:props.active?"#EFF3F6":null
    }
    let total=0
    if(props.order){
        for(let i=0;i<props.order.length;i++){
            total+=props.order[i].totalAmount
        }
    }
    return(
    <ul onClick={props.clicked} style={styled} className={ Style.CustomerListElement }>
        <li><SmallImage addImgHandler={()=>{return null}} imgUrl={props.img} /> </li>
        <li>{props.name}</li>
        <li>{props.contact}</li>
        <li>{props.order?props.order.length:0} </li>
        <li>₹ {total} </li>
        <li>5 oct 2020 </li>
        <li>nil</li>
    </ul>
    )
}

export default CustomerListElement;