import React from 'react';
import Style from './Nav.module.css'
import logo from '../../assets/images/logo.svg'
import {NavLink}  from 'react-router-dom'
function Nav() {
    const logout = ()=>{
        localStorage.clear()
        window.location.href = "https://nearst.store/"
    }
  return (
<nav>
        <div className={ Style.logo } onClick={()=>{window.location.href = "https://nearst.store/"}}><img src={ logo } alt="" /></div>
        <ul>
            <li>
                <ul>
                    <li><NavLink to="" exact activeClassName={Style.active}> Dashboard <span></span></NavLink></li>
                    <li><NavLink to="/orders" activeClassName={Style.active}> Orders <span></span></NavLink></li>
                    <li><NavLink to="/products" activeClassName={Style.active}>  Products / Inventory <span></span></NavLink></li>
                    <li><NavLink to="/seller" activeClassName={Style.active}>  Seller Details <span></span></NavLink></li>
                    <li><NavLink to="/customers" activeClassName={Style.active}>  Customer details <span></span></NavLink></li>
                    <li><NavLink to="/payment" activeClassName={Style.active}> Payment Page <span></span></NavLink></li>
                    <li><NavLink to="/extras" activeClassName={Style.active}> Extras <span></span></NavLink></li>
                </ul>
            </li>
            <li className={ Style.login }>
                <div onClick={logout}>
                    Log Out
                <span></span>
                </div>
                
            </li>
        </ul>
    </nav>
  );
}

export default Nav;
