import React, { Component, Fragment } from 'react'
import Style from './Products.module.css'
import ListBar from '../../component/ListBar/ListBar'
import FixedBar from '../../component/FixedBar/FixedBar'
import ProductDetails from '../../component/ProductDetails/ProductDetails'
import firebase from '../../Firebase/index'
import InputForm from '../../component/InputForm/InputForm'
import ImageInput from '../../component/ImageInput/ImageInput'
class Products extends Component{

    productChangeHandler = (index)=>{
        this.setState({product:this.state.products[index],activeProduct:index,unit:this.state.units[this.state.products[index].productId],addCat:false,addSize:false})
        let merch = null
        this.state.merchants.forEach((x)=>{if(this.state.products[index].merchantId===x.value)
            merch = x.name
        })
        this.setState({merch:merch})
    }
    SearchHandler = (e)=>{
        this.setState({search:e.target.value})
    }
    inputChangeHandler = (e,tag) =>{
        let productCopy = {
            ...this.state.product
        }
        productCopy[tag] = e.target.value
        if(tag==="discount"){
            productCopy["sellingPrice"] = productCopy["offerPrice"] * (1 - (productCopy["discount"]/100))
        }
        else if(tag==="sellingPrice" || tag==="offerPrice"){
            productCopy["discount"] = 100 - productCopy["sellingPrice"]/productCopy["offerPrice"]*100
        }
        this.setState({product:productCopy})
    }
    unitChangeHandler = (e,tag)=>{
        let unitsCopy = {...this.state.unit}
        let productsUnitsSizeCopy = {...unitsCopy[tag]}
        productsUnitsSizeCopy["unit"] = e.target.value
        unitsCopy[tag] = productsUnitsSizeCopy
        this.setState({unit:unitsCopy})
    }
    productSaveChange = ()=>{
        let product = {...this.state.product}
        delete product.productId
        firebase.db.collection('products').doc(this.state.product.productId).set({
            ...product
        })
        .then(()=>{
            this.getProducts()
        })
        if(this.state.units[this.state.product.productId]){
            let lis = Object.keys(this.state.unit)
            for(let i=0; i<lis.length;i++){
                if(this.state.unit[lis[i]].unitId!==null){
                    firebase.db.collection('size_unit').doc(this.state.unit[lis[i]].unitId).set({
                        productId:this.state.product.productId,
                        size:lis[i],
                        unit:this.state.unit[lis[i]].unit
                    })
                    .then(()=>{
                        this.getUnits()
                    })
                }
                else{
                    firebase.db.collection('size_unit').add({
                        productId:this.state.product.productId,
                        size:lis[i],
                        unit:this.state.unit[lis[i]].unit
                    })
                    .then(()=>{
                        this.getUnits()
                    })
                }
            }
        }
    }
    productSaveNew = ()=>{
        firebase.db.collection('products').add({
            ...this.state.product
        })
        .then((docRef)=>{
            this.getProducts()
            this.setState({activeAdd:false})
        })
    }
    productDelete = ()=>{
        firebase.db.collection('products').doc(this.state.product.productId).delete()
        .then(()=>{
            this.getProducts()
            let product = {
                barcode:null,
                brands:[],
                categories:[],
                discount:0,
                fabrics:[],
                isActive:true,
                isReturnable:false,
                merchantId:null,
                offerPrice:0,
                productDetail:"",
                productImage:[],
                productName:"",
                sellingPrice:0,
                sizeUnit:[],
                sleeves:[]
            }
            this.setState({product:product,activeProduct:null})
        })
    }
    getProducts = ()=>{
        let products = []
        firebase.db.collection('products').get()
        .then(querySnapshot => {
        querySnapshot.forEach( doc => {
            let data = {
                ...doc.data(),
                productId: doc.id
            }
            if(!data.brands){
                data.brands = []
            }
            if(!data.fabrics){
                data.fabrics = []
            }
            if(!data.colors){
                data.colors = []
            }
            if(!data.patterns){
                data.patterns = []
            }
            if(!data.neckline){
                data.neckline = []
            }
            if(!data.sleeves){
                data.sleeves = []
            }
            if(!data.styles){
                data.styles = []
            }
            if(!data.works){
                data.works = []
            }
            if(!data.rises){
                data.rises = []
            }
            if(!data.soles){
                data.soles = []
            }
            if(!data.occasions){
                data.occasions = []
            }
            if(!data.heels){
                data.heels = []
            }
            if(data.product){}
            else{
                products.push(data)
            }
        })
        this.setState({products:products})
      })
    }
    getUnits = ()=>{
        let units = {}
        firebase.db.collection('size_unit').get()
        .then(querySnapshot =>{
            querySnapshot.forEach( doc=>{
                let data = doc.data()
                if(!units[data.productId])
                    units[data.productId]={}
                units[data.productId][String(data.size)] = {
                    unit:data.unit,
                    unitId:doc.id
                }
            })
            this.setState({units:units})
        })
    }
    constructor(props){
        super(props);
        this.state = {
            products:null,
            allCategories:null,
            allBrands:null,
            product:{
                barcode:null,
                brands:[],
                categories:[],
                discount:0,
                fabrics:[],
                isActive:true,
                isReturnable:false,
                merchantId:null,
                offerPrice:0,
                productDetail:"",
                productImage:[],
                productName:"",
                sellingPrice:0,
                sizeUnit:[],
                sleeves:[]
            },
            activeProduct:null,
            activeAdd:false,
            search:'',
            addCat:false,
            addSize:false,
            addImg:false,
            merchants:[],
            merch:null,
            units:null,
            unit:null,
            allCategoriesObj:null
            }
        this.getProducts()
        this.getUnits()
      firebase.db.collection('other').doc('categories').get()
      .then(doc => {
          let categories = ["none",]
          this.setState({allCategoriesObj:doc.data().categories})
            doc.data().categories.forEach(cat=>{
                if( cat.subCat ){
                    categories = [...categories, ...cat.subCat]
                }
                categories.push(cat.name)
            })
            let complete = []
            for(let i=0;i<categories.length;i++){
                complete.push({
                    name:categories[i],
                    value:categories[i]
                })
            }
          this.setState({allCategories:complete})
      })
      firebase.db.collection('other').doc('brands').get()
      .then(doc => {
          let brands = [{name:"none",value:"none"},]
            doc.data().brands.forEach(brand=>{
                brands.push({
                    name:brand,
                    value:brand
                })
            })
          this.setState({allBrands:brands})
      })
      firebase.db.collection('other').doc('color').get()
      .then(doc => {
          let color = [{name:"none",value:"none"},]
            doc.data().color.forEach(col=>{
                color.push({
                    name:col,
                    value:col
                })
            })
          this.setState({allColors:color})
      })
      firebase.db.collection('other').doc('fabrics').get()
      .then(doc => {
          let fabrics = [{name:"none",value:"none"},]
            doc.data().fabrics.forEach(fabric=>{
                fabrics.push({
                    name:fabric,
                    value:fabric
                })
            })
          this.setState({allFabrics:fabrics})
      })
      firebase.db.collection('other').doc('occasion').get()
      .then(doc => {
          let occasion = [{name:"none",value:"none"},]
            doc.data().occasion.forEach(occ=>{
                occasion.push({
                    name:occ,
                    value:occ
                })
            })
          this.setState({allOccasions:occasion})
      })
      firebase.db.collection('other').doc('style').get()
      .then(doc => {
          let style = [{name:"none",value:"none"},]
            doc.data().style.forEach(styl=>{
                style.push({
                    name:styl,
                    value:styl
                })
            })
          this.setState({allStyles:style})
      })
      firebase.db.collection('other').doc('pattern').get()
      .then(doc => {
          let pattern = [{name:"none",value:"none"},]
            doc.data().pattern.forEach(pat=>{
                pattern.push({
                    name:pat,
                    value:pat
                })
            })
          this.setState({allPatterns:pattern})
      })
      firebase.db.collection('other').doc('neckline').get()
      .then(doc => {
          let neckline = [{name:"none",value:"none"},]
            doc.data().neckline.forEach(neck=>{
                neckline.push({
                    name:neck,
                    value:neck
                })
            })
          this.setState({allNecklines:neckline})
      })
      firebase.db.collection('other').doc('sleeves').get()
      .then(doc => {
          let sleeves = [{name:"none",value:"none"},]
            doc.data().sleeves.forEach(sleeve=>{
                sleeves.push({
                    name:sleeve,
                    value:sleeve
                })
            })
          this.setState({allSleeves:sleeves})
      })
      firebase.db.collection('other').doc('size').get()
      .then(doc => {
          let size = [{name:"none",value:"none"},]
            doc.data().size.forEach(siz=>{
                size.push({
                    name:siz,
                    value:siz
                })
            })
          this.setState({allSizes:size})
      })
      firebase.db.collection('merchant').get()
      .then(querySnapshot => {
        let merchants = [{name:"none",value:"none"}]
          querySnapshot.forEach(
              doc =>{
                merchants.push({
                    name:doc.data().storeName,
                    value:doc.id
                })
              }
          )
            this.setState({merchants:merchants})
      })
    }
    showAddCat = () =>{
        this.setState({addCat:true})
    }
    showAddSize = ()=>{
        this.setState({addSize:true})
    }
    addCat = (value)=>{
        let categories = [...this.state.product.categories]
        let categoriesList = []
        categories.forEach(cat=>{
            if( cat.subCat ){
                categoriesList = [...categoriesList, ...cat.subCat]
            }
            categoriesList.push(cat.name)
        })
        this.state.allCategoriesObj.forEach((cat,index)=>{
            if(cat.name===value && !categoriesList.includes(value)){
                categories.push({name:value})
                let productCopy={
                    ...this.state.product,
                    categories:categories
                }
                this.setState({product:productCopy})
            }
            else if(cat.subCat){
                if(cat.subCat.includes(value) && !categoriesList.includes(value)){
                    let flag = 0
                    categories.forEach((catCopy,index)=>{
                        if(catCopy.name===cat.name){
                            let catCopyCopy = {
                                ...categories[index]
                            }
                            let catCopyCopySubcat = [...catCopyCopy.subCat]
                            catCopyCopySubcat.push(value)
                            catCopyCopy.subCat=catCopyCopySubcat
                            categories[index] = catCopyCopy
                            let productCopy={
                                ...this.state.product,
                                categories:categories
                            }
                            this.setState({product:productCopy})
                            flag=1
                        }
                        else{
                            if(index===categories.length-1 && flag===0){
                                categories.push({
                                    name:cat.name,
                                    subCat:[value,]
                                })
                                let productCopy={
                                    ...this.state.product,
                                    categories:categories
                                }
                                this.setState({product:productCopy})
                            }
                        }
                    })
                }
            }
        })
        let productCopy = {
            ...this.state.product,
            categories:categories
        }
        this.setState({product:productCopy,addCat:false})
    }
    addSize = (value)=>{
        let unitsCopy={...this.state.unit}
        if(!Object.keys(unitsCopy).includes(value)){
            unitsCopy[value]={
                productId:this.state.product.productId,
                unit:0,
                unitId:null
            }
            this.setState({unit:unitsCopy,addSize:false})
        }
        else{
            this.setState({addSize:false})
        }
    }
    addMerch = (value)=>{
        let productCopy = {
            ...this.state.product,
            merchantId:value
        }
        this.setState({product:productCopy})
    }
    addImgHandler = ()=>{
        this.setState({addImg:true})
    }
    deleteImageHandler = (index)=>{
        let productCopy = {
            ...this.state.product
        }
        let productImageCopy = [...productCopy.productImage]
        productImageCopy.splice(index,1)
        productCopy.productImage = productImageCopy
        this.setState({product:productCopy})
    }
    uploadImgHandler = (file)=>{
        let ref = firebase.storage.ref()
        let fileName = new Date() + '-' + file.name
        let metadata = {contentType:file.type}
        let task = ref.child("data/user/0/com.vendor_app_shopping/cache"+fileName).put(file,metadata)
        task
        .then(snapShot => snapShot.ref.getDownloadURL()
        .then(url =>{
            let productImage = [...this.state.product.productImage,url]
            let productCopy = {
                ...this.state.product,
                productImage:productImage
            }
            this.setState({product:productCopy,addImg:false})
        }))

    }
    filterChangeHandler = (filter,value)=>{
        let productCopy={...this.state.product}
        productCopy[filter] = value==="none"?[]:[value,]
        this.setState({product:productCopy})
    }
    newProductHandler = ()=>{
        let product = {
            barcode:null,
            categories:[],
            discount:0,
            isActive:true,
            isReturnable:false,
            merchantId:null,
            offerPrice:0,
            productDetail:"",
            productImage:[],
            productName:"",
            sellingPrice:0,
            fabrics:[],
            brands:[],
            sleeves:[],
            styles:[],
            occasions:[],
            colors:[],
            neckline:[],
            patterns:[],
            heels:[],
            rises:[],
            soles:[],
            works:[]
        }
        this.setState({product:product,activeProduct:null,activeAdd:true})
    }
    CloseModal = ()=>{
        if(this.state.addImg===true){
            this.setState({addImg:false})
        }
        else{
            this.setState({activeAdd:false})
        }
    }
    render(){
        return(
            <Fragment>
                {this.state.products===null||this.state.units===null?null:
                (<div className={ Style.Product }>
                    <ListBar productChangeHandler={this.productChangeHandler} units={this.state.units} newProductHandler={this.newProductHandler} searchHandler={this.SearchHandler} searchString={this.state.search} activeProduct={this.state.activeProduct} list={this.state.products} />
                    <FixedBar>
                        {this.state.activeAdd?null:<ProductDetails 
                            deleteImageHandler={this.deleteImageHandler} 
                            productDelete={this.productDelete} 
                            unitChangeHandler={this.unitChangeHandler} 
                            addImgHandler={this.addImgHandler} 
                            productSaveChange={this.productSaveChange} 
                            addprod={false} 
                            addCat={this.addCat} 
                            addSize={this.addSize}
                            showAddCat={this.showAddCat} 
                            showAddSize={this.showAddSize}
                            units={this.state.unit} 
                            addcat={this.state.addCat} 
                            addsize={this.state.addSize}
                            allCategories={this.state.allCategories} 
                            product={this.state.product} 
                            merch = {this.state.merch} 
                            inputChangeHandler={this.inputChangeHandler} 
                            allSizes={this.state.allSizes}
                            allBrands={this.state.allBrands} 
                            allColors={this.state.allColors} 
                            allFabrics={this.state.allFabrics}
                            allOccasions={this.state.allOccasions}
                            allStyles={this.state.allStyles}
                            allPatterns={this.state.allPatterns}
                            allNecklines={this.state.allNecklines}
                            allSleeves={this.state.allSleeves}
                            filterChangeHandler={this.filterChangeHandler}
                        />}
                    </FixedBar>
                    {this.state.activeAdd?(<InputForm CloseModal={this.CloseModal}>
                    <ProductDetails 
                            addMerch={this.addMerch} 
                            deleteImageHandler={this.deleteImageHandler} 
                            addImgHandler={this.addImgHandler} 
                            merchants={this.state.merchants} 
                            productSaveChange={this.productSaveNew} 
                            addprod={true} 
                            addCat={this.addCat}
                            addSize={this.addSize} 
                            showAddCat={this.showAddCat} 
                            showAddSize={this.showAddSize}
                            addcat={this.state.addCat} 
                            addsize = {this.state.addSize}
                            allCategories={this.state.allCategories} 
                            product={this.state.product} 
                            inputChangeHandler={this.inputChangeHandler}   
                            allSizes={this.state.allSizes}                          
                            allBrands={this.state.allBrands} 
                            allColors={this.state.allColors} 
                            allFabrics={this.state.allFabrics}
                            allOccasions={this.state.allOccasions}
                            allStyles={this.state.allStyles}
                            allPatterns={this.state.allPatterns}
                            allNecklines={this.state.allNecklines}
                            allSleeves={this.state.allSleeves} 
                            filterChangeHandler={this.filterChangeHandler}
                        />
                    </InputForm>):null}
                    {this.state.addImg?<InputForm CloseModal={this.CloseModal}><ImageInput uploadImgHandler={this.uploadImgHandler}/></InputForm>:null}
                </div>)}
                    
            </Fragment>
        );
    }
}

export default Products;