import React from 'react'
import Style from './SubHead.module.css'
const SubHead = ()=>{
    return(
            <ul className={ Style.SubHead }>
                <li>Image</li>
                <li>SKU</li>
                <li>Product name</li>
                <li>Price</li>
                <li>Product Category</li>
                <li>Total Units</li>
            </ul>
    );
}

export default SubHead