import React from 'react'
import Style from './CustomerSubHead.module.css'
const CustomerSubHead = ()=>{
    return(
            <ul className={ Style.SubHead }>
                <li>Image</li>
                <li>Name</li>
                <li>Contact</li>
                <li>#Purchase</li>
                <li>₹Purchase</li>
                <li>Last Activity</li>
                <li>visits/month</li>
            </ul>
    );
}

export default CustomerSubHead;