import React, { Component, Fragment } from 'react'
import Style from './Payment.module.css'
import PaymentBar from '../../component/PaymentBar/PaymentBar'
import FixedBar from '../../component/FixedBar/FixedBar'
import PaymentDetails from '../../component/PaymentDetails/PaymentDetails'
import firebase from '../../Firebase/index'
class Payment extends Component{
    constructor(props){
        super(props);
        this.state = {
            sellers:null,
            seller:{
                address:null,
                bannerImage:null,
                categories:[],
                cgst:null,
                closingTiming:null,
                openingTiming:null,
                igst:null,
                invoiceNumber:null,
                landlineNumber:null,
                merchantId:null,
                onlinePaymentCharge:null,
                shippingCharge:null,
                storeName:null,
                userName:null,
                userPhone:null,
                commission:0,
                phoneNumber2:""
            },
            input:{
                refId:'',
                amount:0
            },
            activeSeller:null,
            orders:null,
            search:''
            }
        this.getSellers()
    }
    SellerChangeHandler = (index)=>{
        this.setState({seller:this.state.sellers[index],activeSeller:index})
    }
    SearchHandler = (e)=>{
        this.setState({search:e.target.value})
    }
    updateSeller = (seller,totalDue,comList)=> {
        console.log(comList)
        firebase.db.collection('merchant').doc(seller).set({
            totalDueCommission:totalDue,
            lastCalcCommission:firebase.timeStamp.fromDate(new Date()),
            dailyCommissionList:comList
        },{ merge: true })
        .then(()=>{
            this.getOrders(false)
            this.getSellers(false)
        })
    }
    updatePayment = (e) => {
        e.preventDefault()
        firebase.db.collection('merchant').doc(this.state.seller.merchantId).set({
            totalDueCommission:this.state.seller.totalDueCommission-this.state.input.amount,
        },{ merge: true })
        .then(()=>{
            firebase.db.collection('payments').add({
                amount:this.state.input.amount,
                merchantId:this.state.seller.merchantId,
                paymentDate:firebase.timeStamp.fromDate(new Date()),
                referenceId:this.state.input.refId
            })
            .then(()=>{
                this.setState({input:{amount:0,refId:''}})
                this.getPayment()
                this.getOrders(false)
                this.getSellers(false)
            })
        })

    }
    getPayment = ()=>{
        let payments = {}
        firebase.db.collection('payments').get()
        .then(querySnapshot => {
        querySnapshot.forEach( doc => {
            let data = {
                ...doc.data(),
            }
            data["paymentDate"] = data.paymentDate.toDate()
            if(data.merchantId && data.merchantId!==null){
                if(!payments[data.merchantId]){
                    payments[data.merchantId]=[]
                }
                payments[data.merchantId].push(data)
            }
        })
        this.setState({payments:payments})
        console.log(payments)
      })
}
    calculateDue = ()=>{
        if(this.state.sellers){
            let sellers = [...this.state.sellers]
            for(let i=0; i<sellers.length;i++){
                let merchant = sellers[i].merchantId
                let due = sellers[i].totalDueCommission
                let dates = []
                if(this.state.orders[merchant])
                {
                    dates = Object.keys(this.state.orders[merchant])
                }
                let dailyList = {}
                if(sellers[i].dailyCommissionList)
                {
                    dailyList = sellers[i].dailyCommissionList
                }
                let toLast = new Date()
                toLast.setDate(toLast.getDate() - 7)
                if(sellers[i].lastCalcCommission){
                    
                    dates.forEach(date =>{
                        let fromLast = sellers[i].lastCalcCommission.toDate()
                        fromLast.setDate(fromLast.getDate() - 7)
                        if(new Date(date) > fromLast && new Date(date) <= toLast){
                            let today = 0
                            this.state.orders[merchant][date].forEach(order=>{
                                if(order.status!=="cancel" && order.status!== "return"){
                                    let rate = order.totalAmount*(sellers[i].commission/100)
                                    today+=rate
                                    
                                }
                            })
                            due += today
                            dailyList[date] = {
                                commissionAmt:today,
                                commission:sellers[i].commission
                            }
                        }
                    })
                }
                else{
                    dates.forEach(date =>{
                        if(new Date(date) <= toLast){
                            let today = 0
                            this.state.orders[merchant][date].forEach(order=>{
                                if(order.status!=="cancel" && order.status!== "return"){
                                    let rate = order.totalAmount*(sellers[i].commission/100)
                                    today += rate
                                }
                            })
                            due+= today
                            dailyList[date] = {
                                commissionAmt:today,
                                commission:sellers[i].commission
                            }
                        }
                    })
                }
            this.updateSeller(merchant,due,dailyList)
            }
        }
    }
    getOrders = (calc=true)=>{
        let orders = {}
        firebase.db.collection('orders').get()
        .then(querySnapshot => {
        querySnapshot.forEach( doc => {
            let data = {
                ...doc.data(),
            }
            let x = data.createdAt.toDate()
            x.setHours(0,0,0,0)
            data["createdAt"] = x
            if(data.merchantId && data.merchantId!==null){
                if(!orders[data.merchantId]){
                    orders[data.merchantId]={}
                }
                if(!orders[data.merchantId][data.createdAt]){
                    orders[data.merchantId][data.createdAt]=[]
                }
                orders[data.merchantId][data.createdAt].push({
                    orderId:doc.id,
                    totalAmount:data.totalAmount,
                    status:data.status,
                })
            }
        })
        this.setState({orders:orders})
        this.getPayment()
        if(calc)
            this.calculateDue()
      })
    }
    getSellers = (calc=true)=>{
        let sellers=[]
        firebase.db.collection('merchant').get()
        .then(querySnapshot =>{
            querySnapshot.forEach(doc=>{
                let data = {
                    ...doc.data(),
                    merchantId:doc.id
                }
                sellers.push(data)
            })
            this.setState({sellers:sellers})
            if(calc)
                this.getOrders()
        })
    }
    inputHandler = (e,name)=>{
        let inputCopy = {...this.state.input}
        inputCopy[name] = e.target.value
        this.setState({input:inputCopy})
    }
    CloseModal = ()=>{
        if(this.state.addImg===true){
            this.setState({addImg:false})
        }
        else{
            this.setState({activeAdd:false})
        }
    }
    render(){
        return(
            <Fragment>
                {this.state.sellers===null?null:
                (<div className={ Style.Payment }>
                    <PaymentBar list={this.state.sellers} orders={this.state.orders} sellerChangeHandler={this.SellerChangeHandler} searchString={this.state.search} SearchHandler={this.SearchHandler} activeSeller={this.state.activeSeller} />
                    <FixedBar>
                        {this.state.seller.merchantId?<PaymentDetails inputHandler={this.inputHandler} updatePayment={this.updatePayment} input={this.state.input} seller={this.state.seller} payments={this.state.payments} orders={this.state.orders} />:null}
                    </FixedBar>
                </div>)}
                    
            </Fragment>
        );
    }
}

export default Payment;