import React from 'react'
import Style from './OrderSubHead.module.css'

const OrderSubHead = ()=>{
    return(
    <ul className={ Style.OrderSubHead }>
        <li>Image</li>
        <li>Order Details</li>
        <li>Price</li>
        <li>Sold By</li>
        <li>Date Of Order</li>
        <li>Status</li>
        <li>Payment</li>
    </ul>
    )
}

export default OrderSubHead