import React from 'react'
import Style from './SmallImage.module.css'

const SmallImage = (props)=>{
    return(
        <div className={Style.SmallImage} onClick={props.imgUrl?null:()=>{props.addImgHandler()}}>
            {props.imgDelete?<div className={Style.Delete} onClick={()=>{props.imgDelete()}}><i class="fas fa-trash"></i></div>:null}
            { props.imgUrl?<img src={props.imgUrl} alt=""/> : <svg width="7" height="7" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
<line x1="13" x2="13" y2="25" stroke="#085076"/>
<line y1="12" x2="25" y2="12" stroke="#085076"/>
</svg>


 } 
        </div>
    )
}

export default SmallImage
