import React from 'react';
import Style from './InputTag.module.css'
function InputTag(props) {
    let styled = {
        width:props.width,
        height:props.height,
        marginRight:props.margin?props.margin:null
    }
    let inp= null
    if(props.type==="box"){
        inp = <textarea name={props.name} placeholder={props.name} onChange={(e)=>{props.inputChange(e,props.tagName)}} value={props.value} />
    }
    else{
        inp = <input type="text" name={props.name} placeholder={props.name} onChange={(e)=>{props.inputChange(e,props.tagName)}} value={props.value} />
    }
  return (
      <div className={ Style.InputTag } style={styled}>
          <label for={props.name}>{props.lable}</label>
          {inp}
      </div>
  );
}

export default InputTag;
