import React from 'react';
import Style from './ListBar.module.css'
import ToolBar from './ToolBar/ToolBar'
import SubHead from './SubHead/SubHead'
import ProductListElement from '../ProductListElement/ProductListElement'
function ListBar(props) {
  return (
      <div className={ Style.ListBar }>
          <ToolBar head="Products" newProductHandler = {props.newProductHandler} searchHandler={props.searchHandler} searchString={props.searchString} placeholder="Search SKU/Barcode" />
          <SubHead />
          <div className={Style.list}>
              {props.list.map((ele,index)=>{
                if(ele.barcode.includes(props.searchString)){
                  if(index===props.activeProduct){
                    return(<ProductListElement img={ele.productImage[0]} clicked={()=>{return props.productChangeHandler(index)}} units={props.units[ele.productId]} sku={ele.barcode} active name={ele.productName} offprice={ele.offerPrice} sellprice={ele.sellingPrice} cat={ele.categories}/>)
                  }
                  else{
                    return(<ProductListElement img={ele.productImage[0]} clicked={()=>{return props.productChangeHandler(index)}} units={props.units[ele.productId]} sku={ele.barcode} name={ele.productName} offprice={ele.offerPrice} sellprice={ele.sellingPrice} cat={ele.categories}/>)
                  }
                }
                else{
                  return null
                }
              })}
          </div>
      </div>
  );
}

export default ListBar;
