import React from 'react'
import Style from './SellerSubHead.module.css'
const SellerSubHead = ()=>{
    return(
            <ul className={ Style.SellerSubHead }>
                <li>Banner</li>
                <li>Store Name</li>
                <li>Opening Time</li>
                <li>Closing Time</li>
                <li>#products</li>
                <li>#orders</li>
            </ul>
    );
}

export default SellerSubHead