import React from 'react'
import Style from './OrderListElement.module.css'
import OrderProductElement from './OrderProductElement/OrderProductElement'

const OrderListElement = (props)=>{
    let styled = {
        backgroundColor:props.active?"#EFF3F6":null
    }
    return(
        <div style={styled} onClick={props.clicked} className={Style.OrderListElement}>
            <div className={Style.head}>
                <span>Order No: {props.id} </span>
                <span>Mode: {props.mode===true?"Online":"offline"}</span>
            </div>
            {props.products.map(product=>{return <OrderProductElement merchants={props.merchants} product={product} payment={props.payment} date={props.date} status={props.status}  merchant={props.merchant} />})}
        </div>
    )
}

export default OrderListElement