import React, {Fragment} from 'react'
import Style from './PriceList.module.css'

const PriceList = (props)=>{
    let ifselling = [<span className={Style.Selling}> ₹{props.sellprice} </span>,<span className={ Style.Real }> ₹{props.offprice} </span>]
    return(<Fragment>
        {props.offprice?ifselling:<span>₹{props.sellprice}</span>}
        </Fragment>
    )
}

export default PriceList