import React from 'react'
import SmallImage from '../SmallImage/SmallImage'
import Style from './ImageInput.module.css'

const ImageInput = (props) => {
    const upload = (e)=>{
        e.preventDefault();
        let imag = document.querySelector('#image')
        if(imag.files[0]){
            for(let i=0;i<imag.files.length;i++)
                props.uploadImgHandler(imag.files[i])
        }
        else{
            return null;
        }
    }
    return(<div className={Style.ImageInput}>
        <div className={Style.Img}><SmallImage addImgHandler={()=>{return null}} /></div>
        <div  className={Style.Input}><input id="image" type="file" multiple></input></div>
        <button onClick={(e)=>{upload(e)}} className={Style.Button}>Submit</button>
    </div>)
}

export default ImageInput;