import React from 'react'
import InputTag from '../InputTag/InputTag'
import SmallImage from '../SmallImage/SmallImage'
import Style from './PaymentDetails.module.css'

const PaymentDetails = (props)=>{
    let today = new Date()
    let todayTotal = 0
    today.setDate(today.getDate() - 7) 
    today.setHours(0,0,0,0)
    if(props.seller.dailyCommissionList[today]){
        todayTotal = props.seller.dailyCommissionList[today]["commissionAmt"]
    }
    return(
        <div className={Style.PaymentDetails}>
            <h1>Payment Details</h1>
            <div className={Style.Info}>
                <SmallImage addImgHandler={()=>{return null}} imgUrl={props.seller.bannerImage} />
                <div className={Style.InfoList}>
                    <div>Merchant Name: {props.seller.storeName}</div>
                    <div>Total due: {props.seller.totalDueCommission}</div>
                    <div className={Style.Red}>Previous Due: {props.seller.totalDueCommission - todayTotal}</div>
                </div>
            </div>
            {props.orders[props.seller.merchantId]?props.orders[props.seller.merchantId][today]?<div className={Style.Today}>
                <h1>{today.getDate()+' - '+today.getMonth()+' - '+today.getFullYear()} 's Breakdown</h1>
                <ul className={Style.SubHead}>
                    <li>Order Id</li>
                    <li>Status</li>
                    <li>Amount</li>
                    <li>Commission</li>
                </ul>
                <ul className={Style.AllOrders}>
                    {props.orders[props.seller.merchantId][today].map(ele=>{
                        return (<li>
                            <ul style={{color:(ele.status==="return"||ele.status==="cancel")?"red":null}} className={Style.Order}>
                                <li>{ele.orderId}</li>
                                <li>{ele.status}</li>
                                <li>₹ {ele.totalAmount}</li>
                                <li>₹ {(ele.status==="return"||ele.status==="cancel")?0:ele.totalAmount*(props.seller.commission/100)}</li>
                            </ul>
                        </li>)
                    })}
                </ul>
                <div className={Style.TotalToday}>Total Commission: ₹ {todayTotal}</div>
            </div>:<h1 style={{textAlign:'center'}}>No Orders On {today.getDate()+' - '+today.getMonth()+' - '+today.getFullYear()}</h1>:<h1 style={{textAlign:'center'}}>No Orders On {today.getDate()+' - '+today.getMonth()+' - '+today.getFullYear()}</h1>}
            <div className={Style.NewPayment}>
                <h1>New Payment</h1>
                <div className={Style.NewPay}>
                    <InputTag inputChange={props.inputHandler} width="20%" height="30px" type="text" tagName="amount" lable="Amount" name="Amount" value={props.input.amount} />
                    <InputTag inputChange={props.inputHandler} width="40%" height="30px" type="text" tagName="refId" lable="Reference" name="Reference" value={props.input.refId} />
                    <button onClick={(e)=>{props.updatePayment(e)}}>Pay</button>
                </div>
                <h1>Previous Payments</h1>
                    {props.payments[props.seller.merchantId]?<ul className = {Style.PrevSub}>
                        <li>Reference Id</li>
                        <li>Date</li>
                        <li>Amount</li>
                    </ul>:null}
                    <ul className={Style.PayList}>
                    {props.payments[props.seller.merchantId]?props.payments[props.seller.merchantId].map(ele=>{
                        return (<li>
                            <ul className={Style.Pay}>
                                <li>{ele.referenceId}</li>
                                <li>{ele.paymentDate.getDate()+' - '+ele.paymentDate.getMonth()+' - '+ele.paymentDate.getFullYear()}</li>
                                <li>₹ {ele.amount}</li>
                            </ul>
                        </li>)
                    }):<h3 style={{textAlign:"center"}}>No Payments Yet</h3>}
                    </ul>

            </div>
        </div>
    )
}

export default PaymentDetails 