import React, { Fragment } from 'react'
import Style from './InputForm.module.css'

const InputForm = (props)=>{
    return(
        <Fragment>
            <div onClick={()=>{props.CloseModal()}} className={Style.Modal}>
            </div>
            <div className={Style.form}>
                {props.children}
            </div>
        </Fragment>
    )
}

export default InputForm;