import React from 'react';
import Style from './DropDown.module.css'
function DropDown(props) {
  let styled = {
    width:props.width?props.width:null,
    display:props.display?props.display:null
  }
  return (props.values && props.value!==undefined?(
    <div className={ Style.DropDown } style={styled}>
    <label for={props.name} > {props.name} </label>
      <select style={styled} onChange={(e)=>{props.dropChange(props.name,e.target.value)}} value={props.value.length===0?"none":props.value[0]} name={props.name} id="cars">
          {props.values.map(val=>{
          return <option value={val.value}>{val.name}</option>
          })}
      </select>
  </div>
  ):props.values?(
    <div className={ Style.DropDown }>
    <label for={props.name} > {props.name} </label>
      <select style={styled} onChange={(e)=>{props.dropChange(e.target.value)}} name={props.name} id="cars">
          {props.values.map(val=>{
          return <option value={val.value}>{val.name}</option>
          })}
      </select>
  </div>
  ):(    <div className={ Style.DropDown }>
    <label for={props.name} > {props.name} </label>
      <select style={styled} name={props.name} id="cars">
          <option value="none">None</option>
          <option value="none">None</option>
          <option value="none">none</option>
          <option value="none">none</option>
      </select>
  </div>)

  );
}

export default DropDown;
