import React, { Fragment } from 'react'
import Style from './SellerDetails.module.css'
import SmallImage from '../../component/SmallImage/SmallImage'
import InputTag from '../InputTag/InputTag'
import DropDown from '../DropDown/DropDown'
const SellerDetails = (props)=>{
    const makeCategory = (cat,index)=>{
        if(index===0){
            return <InputTag width="20%" margin="7px" height="30px" name={'cat' + index} type="text" lable="categories" value={cat}/>
        }
        else{
            return <InputTag width="20%" margin="7px" height="30px" name={'cat' + index} type="text" value={cat}/>
        }
    }
    const details = (
        <div className={Style.SellerDetails}>
        <h1>Seller Details</h1>
        <div className={Style.imageList}>
            {props.seller.bannerImage?<SmallImage addImgHandler={()=>{return null}}  imgUrl={props.seller.bannerImage} />:<SmallImage addImgHandler={props.addImgHandler}  imgUrl={props.seller.bannerImage} />}
        </div>
        <div className={Style.Basic} > 
            <InputTag inputChange={props.inputChangeHandler} width="40%" type="text" height="50px" name="name" tagName="storeName" lable="Store Name" value={props.seller.storeName}/> 
            <InputTag inputChange={props.inputChangeHandler} lable="cgst" type="number" width="13%" height="35PX" tagName="cgst" name="cgst" value={props.seller.cgst}/> 
            <InputTag inputChange={props.inputChangeHandler} lable="igst" type="number" width="13%" height="35px" tagName="igst" name="igst" value={props.seller.igst}/> 
            <InputTag inputChange={props.inputChangeHandler} lable="Shipping" type="number" width="13%" height="35px" tagName="shippingCharge" name="shipping" value={props.seller.shippingCharge}/> 
            <InputTag inputChange={props.inputChangeHandler} lable="online" type="number" width="13%" height="35px" tagName="onlinePaymentCharge" name="online" value={props.seller.onlinePaymentCharge}/> 
        </div>
        <div > 
            <InputTag inputChange={props.inputChangeHandler} width="100%" height="80px" name="desc" type="box" tagName="address" lable="store address" value={props.seller.address}/> 
        </div>
        <div className={Style.LeftAlign} > 
            <InputTag inputChange={props.inputChangeHandler} width="45%" height= "30px" type="text" tagName="openingTiming" lable="Opening Timing" name="openingTiming" value={props.seller.openingTiming}/>
            <InputTag inputChange={props.inputChangeHandler} width="45%" height= "30px" type="text" tagName="closingTiming" lable="Closing Timing" name="closingTiming" value={props.seller.closingTiming}/>
       </div>
        <div className={Style.LeftAlign} > 
            <InputTag inputChange={props.inputChangeHandler} width="45%" height= "30px" type="text" tagName="userName" lable="User Name" name="user name" value={props.seller.userName}/>
            <InputTag inputChange={props.inputChangeHandler} width="45%" height= "30px" type="text" tagName="userPhone" lable="User Phone" name="user phone" value={props.seller.userPhone}/>
       </div>
        <div className={Style.LeftAlign} > 
            <InputTag inputChange={props.inputChangeHandler} width="45%" height= "30px" type="text" tagName="phoneNumber2" lable="Phone Number 2" name="Phone Number 2" value={props.seller.phoneNumber2}/>
            <InputTag inputChange={props.inputChangeHandler} width="45%" height= "30px" type="text" tagName="commission" lable="commission" name="commission" value={props.seller.commission}/>
       </div>
        <div className={Style.LeftAlign} > 
            <InputTag inputChange={props.inputChangeHandler} width="45%" height= "30px" type="text" tagName="landlineNumber" lable="Landline Number" name="Landline Number" value={props.seller.landlineNumber}/>
            <InputTag inputChange={props.inputChangeHandler} width="45%" height= "30px" type="text" tagName="invoiceNumber" lable="Invoice Number" name="Invoice Number" value={props.seller.invoiceNumber}/>
       </div>
        <div> 
            {props.seller.categories.length ===0?<div className={Style.Bold}>Categories</div>:props.seller.categories.map((cat,index)=>{return makeCategory(cat,index)})} 
            {!props.addcat?(<div className={Style.add} onClick={()=>{props.showAddCat()}} > 
                <svg width="12" height="12" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <line x1="13" x2="13" y2="25" stroke="#085076"/>
                    <line y1="12" x2="25" y2="12" stroke="#085076"/>    
                </svg> 
            </div> ):<DropDown width="100px" dropChange={props.addCat} display="inline-block" values={props.allCategories}/>}
            
        </div>
        <div>
        <button onClick={props.sellerSaveChange}>Save Changes</button>
        </div>

    </div>
    )
    return(
        <Fragment>
            {(props.seller.merchantId===null)?null:details}
        </Fragment>
    )
}

export default SellerDetails