import React from 'react'
import Style from './OrderProductElement.module.css'
import SmallImage from '../../SmallImage/SmallImage'
import PriceList from '../../PriceList/PriceList'
const OrderProductElement = (props)=>{
    return(
        <ul className={ Style.OrderProductElement }>
        <li><SmallImage imgUrl={props.product.productImage} /> </li>
        <li><div>{ props.product.name }</div><div>Size: {props.product.size}</div><div>Qty: {props.product.quantity}</div></li>
        <li><PriceList offprice={props.product.offprice} sellprice={props.product.price} /></li>
        <li>{props.merchants[props.merchant]}</li>
        <li>{props.date.toDate().toDateString()}</li>
        <li>{props.status}</li>
        <li>{props.payment}</li>
    </ul>
    )
}

export default OrderProductElement