import React from 'react';
import Style from './SellerBar.module.css'
import ToolBar from '../ListBar/ToolBar/ToolBar'
import SellerSubHead from './SellerSubHead/SellerSubHead'
import SellerListElement from '../SellerListElement/SellerListElement'
function SellerBar(props) {
  return (
      <div className={ Style.SellerBar }>
          <ToolBar head="Sellers" newProductHandler = {props.newSellerHandler} searchHandler={props.SearchHandler} searchString={props.searchString} placeholder="Search Store Name" />
          <SellerSubHead />
          <div className={Style.list}>
              {props.list.map((ele,index)=>{
                  console.log(ele)
                if(ele.storeName.includes(props.searchString)){
                    
                  if(index===props.activeSeller){
                    return(<SellerListElement img={ele.bannerImage} storeName={ele.storeName} openingTime={ele.openingTiming} closingTime={ele.closingTiming} products={props.products[ele.merchantId]} orders={props.orders[ele.merchantId]} clicked={()=>{return props.sellerChangeHandler(index)}} active/>)
                  }
                  else{
                    return(<SellerListElement img={ele.bannerImage} storeName={ele.storeName} openingTime={ele.openingTiming} closingTime={ele.closingTiming} products={props.products[ele.merchantId]} orders={props.orders[ele.merchantId]} clicked={()=>{return props.sellerChangeHandler(index)}} />)
                  }
                }
                else{
                  return null
                }
              })}
          </div>
      </div>
  );
}

export default SellerBar;