import React,{Component,Fragment} from 'react'
import Style from './Customer.module.css'
import CustomerBar from '../../component/CustomerBar/CustomerBar'
import CustomerDetails from '../../component/CustomerDetails/CustomerDetails'
import FixedBar from '../../component/FixedBar/FixedBar'
import firebase from '../../Firebase/index'
class Customer extends Component{
    getOrders = ()=>{
        let orders = {}
        firebase.db.collection('orders').get()
        .then(querySnapshot => {
            querySnapshot.forEach( doc => {
                let data={
                    ...doc.data(),
                    orderId:doc.id
                }
              if(orders[data.userId]){
                  orders[data.userId].push(data)
              }
              else{
                  orders[data.userId] = [data,]
              }
            })
            console.log(orders)
            this.setState({orders:orders})
          })
    }
    getSeller = ()=>{
        firebase.db.collection('merchant').get()
        .then(
            querySnapshot =>{
                let merchants={}
                querySnapshot.forEach(doc=>{
                    merchants[doc.id]=doc.data().storeName
                })
                this.setState({merchants:merchants})
            }
        )
    }
    getCustomers = ()=>{
        let customers = []
        firebase.db.collection('User').get()
        .then(querySnapshot => {
        querySnapshot.forEach( doc => {
            let data = {
                ...doc.data(),
                userId: doc.id
            }
            if(data.userName===null){
                data.userName=""
            }
            customers.push(data)
        })
        console.log(customers)
        this.setState({customers:customers})
        this.getOrders()
        this.getSeller()
      })
    }
    constructor(props){
        super(props);
        this.state = {
            customers:null,
            customer:null,
            activeCustomer:null,
            search:'',
            orders:{},
            merchants:null
            }
        this.getCustomers()
    }
    CustomerChangeHandler = (index)=>{
        this.setState({customer:this.state.customers[index],activeCustomer:index})
    }
    SearchHandler = (e)=>{
        this.setState({search:e.target.value})
    }
    render(){
        return(
            <Fragment>
                {this.state.customers===null?null:(            
                <div className={Style.Customer}>
                    <CustomerBar customers={this.state.customers} orders={this.state.orders} searchString={this.state.search} SearchHandler={this.SearchHandler} CustomerChangeHandler={this.CustomerChangeHandler} activeCustomer={this.state.activeCustomer}/>
                    <FixedBar>
                        {this.state.customer?<CustomerDetails customer={this.state.customer} merchants={this.state.merchants} orders={this.state.orders[this.state.customer.userId]} />:null}
                    </FixedBar>
                </div>
                )}
                {/* {this.state.addShow?<InputForm CloseModal={this.modifyDelivery}><InputTag inputChange={this.inputChangeHandler} width="100%" height="80px" name="address" type="box" tagName="address" lable="Delivery Address" value={this.state.order.address}/><button onClick={(e)=>{this.upload(e)}} className={Style.Button}>Submit</button></InputForm>:false} */}
            </Fragment>
        )
    }
}

export default Customer;