import React from 'react'
import Style from './CustomerBar.module.css'
import ToolBar from '../ListBar/ToolBar/ToolBar'
import CustomerSubHead from './CustomerSubHead/CustomerSubHead'
import CustomerListElement from '../CustomerListElement/CustomerListElement'
const CustomerBar = (props)=>{
    return(
        <div className={Style.CustomerBar}>
            <ToolBar head="Customers" newProductHandler = {props.newProductHandler} searchHandler={props.SearchHandler} searchString={props.searchString} placeholder="Search Customer" />
            <CustomerSubHead />
            <div className={Style.list}>
              {props.customers.map((ele,index)=>{
                if(ele.userName.includes(props.searchString)){
                  if(index===props.activeCustomer){
                    return(<CustomerListElement order={props.orders[ele.userId]} clicked={()=>{return props.CustomerChangeHandler(index)}} img={ele.userImage} name={ele.userName} contact={ele.userPhone} active/>)
                  }
                  else{
                    return(<CustomerListElement order={props.orders[ele.userId]} clicked={()=>{return props.CustomerChangeHandler(index)}} img={ele.userImage} name={ele.userName} contact={ele.userPhone} />)
                  }
                }
                else{
                  return null
                }
              })}
            </div>
        </div>
    )
}

export default CustomerBar;