import React from 'react'
import Style from './PaymentSubHead.module.css'
const PaymentSubHead = ()=>{
    return(
            <ul className={ Style.PaymentSubHead }>
                <li>Banner</li>
                <li>Store Name</li>
                <li>Comission Rate</li>
                <li>Total Due</li>
                <li>Payment today</li>
                <li>Last Paid</li>
            </ul>
    );
}

export default PaymentSubHead